.home {
        background-image: url('../img/header.jpg');
        background-image: url('../img/header.webp');

        background-position: center center;
        background-size: cover;
}
.contenido-home {

    position: relative;
    background: linear-gradient(270deg, rgba(255, 166, 0, 0.47) 40%, rgba(0, 0, 0, 0.648) 60%);
    height: 100rem;
    display: flex;
    flex-direction: column;


    h1 {
        text-align: center;
        color: $blanco;
        font-weight: $bold;
        line-height: 1.3;
        max-width: 80rem;
        margin: 5rem 2rem;
    }

    @include tablet {
        min-height: 80rem;
        height: 90%;
        width: 100%;


        h1{        
            text-align: left;
            margin: 5rem 3rem;
            font-size: 4.4rem;
            text-transform: capitalize;
            max-width: 65rem;
        }
    }
}

.homeTitles {
    h2 {
        margin: 12rem 0 6rem;
        text-transform: uppercase;
        @include tablet {
            margin: 12rem 0 6rem;
        }
    }

    h2.ultimoH2{
        margin: 0 0 6rem;
        @include tablet {
            margin: 12rem 0 6rem;
        }
    }

    a{
            h2{
            color: $grisOscuro;
        }
    }

        ul.checks{ 
            margin: 0;
            padding: 0;
            display: flex;
            flex-direction: column;
            list-style: none;
            text-decoration: none;
        }
        li{
            text-align: center;
            font-size: 2rem;
        }
        ul.checks{ 
            li:before {
                content: "\2713"; /* código Unicode para el símbolo de check */
                color: $verde; /* color del símbolo */
                margin-right: 0.5rem; /* espacio entre el símbolo y el texto */
            }
    }
}

.alinear-botonPrincipal{
    text-align: center;
  }