.botonPrincipal {
    @include boton($amarillo, inline-block);
    width:  100%;
    height: 100%;
    text-transform: uppercase;
    margin-top: 6rem;
   
    &:hover{
        font-size: 2rem;
    }
    @include tablet {
        width: 50%;

        &:hover{
            font-size: 1.8rem;
            background-color: $verde;
        }
    }
}

.boton-amarillo {
    @include boton($amarillo, inline-block);
}

.boton-amarillo-block {
    @include boton($amarillo, block);
}

.boton-grisOscuro-block {
    @include boton(darken($turquesa, 30), block);
    border: 0.3rem solid $turquesa;
    line-height: 1.5rem;
    border-radius: 5rem
}

.boton-verde {
    @include boton($verde, inline-block);
}

.boton-verde-block {
    @include boton($verde, inline-block);
}

.boton-turquesa-block {
    @include boton(darken($turquesa, 30), block);
    border: 0.1rem solid $turquesa;
    line-height: 1.5rem;


}
.boton-rojo-block {
    @include boton($rojo, block);
}

.boton-gris-block {
    @include boton($grisOscuro, block);
    width:  100%;
}

.contener-boton {
    width:  100%;
   
    @include tablet {
        width: 25%;
    }
}