.blog {
    margin: 1rem 0;
    padding: 0;

    @include tablet {
        padding: 0 5rem 0 0;
    }
}

.entrada-blog {
    padding: 0 1rem;
  
    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    picture {
        
          img{
              height: 100%;
              object-fit: contain; //Para evitar que la imagen se distorsione

            }
      }

.informacion-meta {
    text-align: center;
    span{
        color: $amarillo;
    }
    @include tablet{
        text-align: start;
    }
}
.texto-entrada {
    height: 30rem;
    margin-top: 2rem;
    overflow: hidden;


    a{
        color: $negro;
        overflow: clip;
        white-space: wrap;

        h3 {
            text-align: center;
            margin: 0;
            line-height: 1.4;
    
            
    
            &::after{
                content: "";
                display: block;
                width: 30rem;
                height: .4rem;
                background-color: $verde;
                margin: .5rem auto 0;
            }
        }
        @include tablet {
            h3 {
                text-align: start;
                line-height: 1.4;
        
                
        
                &::after{
                    margin: .5rem;
                }
            }
        }
    }

    @include tablet {
        margin-top: 0;
    }


}
}
